import React, { useState, useEffect } from "react";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import 'react-credit-cards/es/styles-compiled.css';
import AuthService from "./services/auth.service";
import Login from "./components/Login";
import Register from "./components/Register";
import Home from "./components/Home";
import Profile from "./components/Profile";
import BoardUser from "./components/BoardUser";
import BoardModerator from "./components/BoardModerator";
import BoardAdmin from "./components/BoardAdmin";
import comp_files from "./components/comp_files";
import comp_pivot from "./components/comp_pivot";
import Comp_comp from "./components/comp_comp.js";
import Prices from "./page/price.js";

import ListCatComponent from './components/Cat/ListCatComponent';

import ListExpenseComponent from './components/Expense/ListExpenseComponent';
import CreateExpenseComponent from './components/Expense/CreateExpenseComponent';

import ListTeamComponent from './components/team/ListTeamComponent';
import CreateTeamComponent from './components/team/CreateTeamComponent';
import ViewTeamComponent from './components/team/ViewTeamComponent';
import ListUserComponent from './components/User/ListUserComponent';

import PaymentForm from './components/payment/PaymentForm';

const App = () => {

  const [currentUser, setCurrentUser] = useState(undefined);

  useEffect(() => {
    const user = AuthService.getCurrentUser();

    if (user) {
      setCurrentUser(user);
    }
  }, []);

  const logOut = () => {
    AuthService.logout();
  };

  return (
    <Router>
      <div>
        <nav className="navbar navbar-expand navbar-dark bg-dark">
          <Link to={"/"} className="navbar-brand">
            DeveX
          </Link>
          <div className="navbar-nav mr-auto">
            <li className="nav-item">
              <Link to={"/home"} className="nav-link">
                Home
              </Link>
            </li>
            <li className="nav-item">
              <Link to={"/comp_pivot"} className="nav-link">
                Pivot Tables
              </Link>
            </li>
           
            <li className="nav-item">
              <Link to={"/comp_comp"} className="nav-link">
              BitCoin Price
              </Link>
            </li>
            <li className="nav-item">
              <Link to={"/price"} className="nav-link">
              Prices
              </Link>
            </li>
            <li className="nav-item">
              <Link to={"/cat"} className="nav-link">
              Category
              </Link>
            </li>
            
            <li className="nav-item">
              <Link to={"/expense"} className="nav-link">
              Expenses
              </Link>
            </li>
            <li className="nav-item">
              <Link to={"/team"} className="nav-link">
              Team
              </Link>
            </li>
            <li className="nav-item">
              <Link to={"/PaymentForm"} className="nav-link">
              Payment
              </Link>
            </li>
            {currentUser && (
              <li className="nav-item">
                <Link to={"/user"} className="nav-link">
                  User
                </Link>
              </li>
            )}
          </div>

          {currentUser ? (
            <div className="navbar-nav ml-auto">
              <li className="nav-item">
                <Link to={"/profile"} className="nav-link">
                  {currentUser.username}
                </Link>
              </li>
              <li className="nav-item">
                <a href="/login" className="nav-link" onClick={logOut}>
                  LogOut
                </a>
              </li>
            </div>
          ) : (
            <div className="navbar-nav ml-auto">
              <li className="nav-item">
                <Link to={"/login"} className="nav-link">
                  Login
                </Link>
              </li>

              <li className="nav-item">
                <Link to={"/register"} className="nav-link">
                  Sign Up
                </Link>
              </li>
            </div>
          )}
        </nav>

        <div className="container mt-3">
          <Switch>
            <Route exact path={["/", "/home"]} component={Home} />
            <Route exact path="/login" component={Login} />
            <Route exact path="/register" component={Register} />
            <Route exact path="/profile" component={Profile} />
            <Route path="/user" component={BoardUser} />
            <Route path="/mod" component={BoardModerator} />
            <Route path="/admin" component={BoardAdmin} />
            <Route path="/comp_files" component={comp_files} />
            <Route path="/comp_pivot" component={comp_pivot} />
            <Route path="/comp_comp" component={Comp_comp} />
            <Route path="/price" component={Prices} />
            <Route path = "/cat" component = {ListCatComponent} />
            <Route path = "/expense" component = {ListExpenseComponent} />
            <Route path = "/add-expense/:id" component = {CreateExpenseComponent}></Route>
            <Route path = "/team" component = {ListTeamComponent} />
            <Route path = "/add-team/:team_id" component = {CreateTeamComponent}></Route>
            <Route path = "/view-team/:team_id" component = {ViewTeamComponent}></Route>
            <Route path = "/users" component = {ListUserComponent} />
            <Route path = "/PaymentForm" component = {PaymentForm} />

          </Switch>
        </div>
      </div>
    </Router>
  );
};

export default App;