import React, { Component } from 'react'
import ReactDOM from 'react-dom';
import PivotTableUI from 'react-pivottable/PivotTableUI';
import 'react-pivottable/pivottable.css';
import Children from '../components/children'

// see documentation for supported input formats

const data = [['attribute', 'attribute2'], ['value1', 'value2']];


class BoxesFiles extends Component {
  render(){
    return (
    <div>
      <div class="container">
          <div class="card-deck mb-3 text-center">
            <div class="card mb-4 shadow-sm">
              <div class="card-header">
                <h4 class="my-0 font-weight-normal">EXCEL</h4>
              </div>
              <div class="card-body">
                
               
                <button type="button" class="btn btn-lg btn-block btn-outline-primary">UPLOAD</button>
              </div>
            </div>
            <div class="card mb-4 shadow-sm">
              <div class="card-header">
                <h4 class="my-0 font-weight-normal">CSV</h4>
              </div>
              <div class="card-body">
              
                <button type="button" class="btn btn-lg btn-block btn-primary">UPLOAD</button>
              </div>
            </div>
            <div class="card mb-4 shadow-sm">
              <div class="card-header">
                <h4 class="my-0 font-weight-normal">JSON</h4>
              </div>
              <div class="card-body">
               
                <button type="button" class="btn btn-lg btn-block btn-primary">UPLOAD</button>
              </div>
            </div>
          </div>
        </div>
    </div>
    )
  }
}
export default class CompPivot extends Component {
  constructor(props) {
    super(props);
    this.state = props;
}

render() {
    return (
      <div>
      <h4>Pivot Data</h4>
     < BoxesFiles />
      <Children>show files</Children>
      <br />
        <PivotTableUI
            data={data}
            onChange={s => this.setState(s)}
            {...this.state}
        />
        </div>
    );
}
}