import React, { Component } from 'react'
import TeamService from '../../services/TeamService'

class ViewTeamComponent extends Component {
    constructor(props) {
        super(props)

        this.state = {
            team_id: this.props.match.params.team_id,
            team: {}
        }
    }

    componentDidMount(){
        TeamService.getTeamById(this.state.team_id).then( res => {
            this.setState({team: res.data});
        })
    }
    cancel(){
        this.props.history.push('/team');
    }
    render() {
        return (
            <div>
                <br></br>
                <div className = "card col-md-6 offset-md-3">
                    <h3 className = "text-center"> View team Details</h3>
                    <div className = "card-body">
                        <div className = "row">
                            <label> Employee First Name: </label>
                            <div> { this.state.team.team_name }</div>
                        </div>
                       
                    </div>
                    <button className="btn btn-danger" onClick={this.cancel.bind(this)} style={{marginLeft: "10px"}}>Cancel</button>
                </div>
            </div>
        )
    }
}

export default ViewTeamComponent