import axios from 'axios';
import authHeader from './auth-header';

const TEAM_API_BASE_URL = "/api/team";

class TeamService {

    getTeams(){
        return axios.get(TEAM_API_BASE_URL, { headers: authHeader() });
    }


    createTeam(team){
        return axios.post(TEAM_API_BASE_URL, team,{ headers: authHeader() });
    }

    getTeamById(team_id){
        return axios.get(TEAM_API_BASE_URL + '/' + team_id,{ headers: authHeader() });
    }

    updateTeam(team, team_id){
        return axios.put(TEAM_API_BASE_URL + '/' + team_id, team,{ headers: authHeader() });
    }

    deleteTeam(team_id){
        return axios.delete(TEAM_API_BASE_URL + '/' + team_id,{ headers: authHeader() });
    }
}

export default new TeamService()