import axios from 'axios';
import authHeader from './auth-header';

const EXPENSE_API_BASE_URL = "/api/expenses";

class ExpenseService {

    getExpenses(){
        return axios.get(EXPENSE_API_BASE_URL, { headers: authHeader() });
    }


    createExpense(expense){
        return axios.post(EXPENSE_API_BASE_URL, expense,{ headers: authHeader() });
    }
    deleteExpense(expenseId){
        return axios.delete(EXPENSE_API_BASE_URL + '/' + expenseId,{ headers: authHeader() });
    }
    updateExpense(expense, expenseId){
        return axios.put(EXPENSE_API_BASE_URL + '/' + expenseId, expense,{ headers: authHeader() });
    }
    getExpenseById(expense_id){
        return axios.get(EXPENSE_API_BASE_URL + '/' + expense_id,{ headers: authHeader() });
    }
}

export default new ExpenseService()