import React, { Component } from 'react'
import Moment from 'react-moment';
import DatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";
import ExpenseService from '../../services/ExpenseService'

class ListExpenseComponent extends Component {
    constructor(props) {
        super(props)

        this.state = {
                expenses: []
        }
        this.addExpense = this.addExpense.bind(this);
        this.editExpense = this.editExpense.bind(this);
    }

    componentDidMount(){
        ExpenseService.getExpenses().then((res) => {
            this.setState({ expenses: res.data});
            
        });
    }
    editExpense(id){
        this.props.history.push(`/add-expense/${id}`);
    }
    addExpense(){
        this.props.history.push('/add-expense/_add');
    }
    deleteExpense(id){
        ExpenseService.deleteExpense(id).then( res => {
            this.setState({expenses: this.state.expenses.filter(expense => expense.id !== id)});
        });
    }
    render() {
        return (
            <div>
                 <h2 className="text-center">Expense List</h2>
                 <div className = "row">
                    <button className="btn btn-primary" onClick={this.addExpense}> Add Expense</button>
                 </div>
                 <br></br>
                 <div className = "row">
                        <table className = "table table-striped table-bordered">

                            <thead>
                                <tr>
                                    <th> Expense id</th>
                                    <th> Description</th>
                                    <th> Location</th>
                                    <th> Date</th>
                                    <th> Category</th>
                                    <th> Team</th>
                                    <th> Actions</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    this.state.expenses.map(
                                        expense => 
                                        <tr key = {expense.id}>
                                             <td> { expense.id} </td>
                                             <td> { expense.description} </td> 
                                             <td> { expense.location} </td> 
                                             <td> <Moment date={expense.expensedate} format="YYYY/MM/DD"/></td> 
                                             <td> {expense.category.name} </td> 
                                             <td> {expense.team.team_name} </td> 
                                             <td>
                                             <button onClick={ () => this.editExpense(expense.id)} className="btn btn-info">Update </button>
                                             <button style={{marginLeft: "10px"}} onClick={ () => this.deleteExpense(expense.id)} className="btn btn-danger">Delete </button>
                                                 
                                             </td>
                                        </tr>
                                    )
                                }
                            </tbody>
                        </table>

                 </div>

            </div>
        )
    }
}

export default ListExpenseComponent