import React, { Component } from 'react'
import ExpenseService from '../../services/ExpenseService';
import CatService from '../../services/CatService';
import TeamService from '../../services/TeamService';
import { Table,Container,Input,Button,Label, FormGroup, Form} from 'reactstrap';
import DatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";
import moment from 'react-moment';
import Select from 'react-select';

class CreateExpenseComponent extends Component {
    emptyItem = {
        description : '' ,
        expensedate : new Date(),
        location : '',
        category : {id:1 , name:'Travel'},
        team : {team_id:1,team_name:'test'}
    }
    constructor(props) {
        super(props)

        this.state = {
            // step 2
            id: this.props.match.params.id,
            isLoading :true,
            Categories:[],
            Teams:[],
            Expsenses : [],
            date :new Date(),
            item : this.emptyItem,
           
            expdesc:'',
            expvalue:{id:1 , name:'Travel'},
            expteam:{team_id:1,team_name:'sdfsd'},
            expdate : new Date(),
            explocation : ''
        }
      
        this.saveOrUpdateExpense = this.saveOrUpdateExpense.bind(this);
        this.UpdateCategory= this.UpdateCategory.bind(this);
        this.UpdateTeam= this.UpdateTeam.bind(this);
    
        this.changeExpDescHandler = this.changeExpDescHandler.bind(this);
        this.changeExpCatHandler = this.changeExpCatHandler.bind(this);
        this.changeExpTeamHandler = this.changeExpTeamHandler.bind(this);
        this.handleExpDateChange = this.handleExpDateChange.bind(this);
        this.changeExpLocationHandler = this.changeExpLocationHandler.bind(this);
        
    }
   
    // step 3
    changeExpDescHandler= (event) => {
        this.setState({expdesc: event.target.value});
    }
    changeExpCatHandler(event) {   
        const name = {id :event.target.value};
        console.log('cat' + JSON.stringify(name));
        this.setState({expvalue:name});  
    }
    changeExpTeamHandler(event) {   
        const name = {team_id :event.target.value};
        console.log('team' + JSON.stringify(name));
        this.setState({expteam:name});  
    }
    handleExpDateChange(date){
        this.setState({expdate:date});
    }
    changeExpLocationHandler= (event) => {
        this.setState({explocation: event.target.value});
    }
    saveOrUpdateExpense = (e) => {
        e.preventDefault();
        const item = this.state.item;
       
        let expense = {description: this.state.expdesc, category: this.state.expvalue,team:this.state.expteam, expensedate: this.state.expdate, location: this.state.explocation};
        console.log('expense => ' + JSON.stringify(expense));
       
        if(this.state.id === '_add'){
            ExpenseService.createExpense(expense).then(res =>{
                this.props.history.push('/expense');
            });
        }else{
            console.log("id:",this.state.id);
            console.log("expense:",expense);
            ExpenseService.updateExpense(expense, this.state.id).then( res => {
            this.props.history.push('/expense');
            });
        }
          
       
    }
      UpdateCategory(event){
        console.log("test");
        this.setstate({
        Category :event.target.value});
        }
    UpdateTeam(event){
        console.log("test");
        this.setstate({
        Teams :event.target.value});
    }
       
    cancel(){
        this.props.history.push('/expense');
    }

    getTitle(){
        if(this.state.id === '_add'){
            return <h3 className="text-center">Add Expenseeeess</h3>
        }else{
            return <h3 className="text-center">Update Expense</h3>
        }
    }

    componentDidMount(){
        CatService.getCats().then((res) => {
            this.setState({ Categories: res.data, isLoading: false});
        });
        TeamService.getTeams().then((res) => {
            this.setState({ Teams: res.data, isLoading: false});
        });

        if(this.state.id === '_add'){
            
        }else{
            ExpenseService.getExpenseById(this.state.id).then( (res) =>{
                let expense = res.data;
                
                
                console.log("expense:",expense.category.id);
                let item={...this.state.expvalue};
                this.setState({ 
                    expdesc: expense.description,
                    
                    //catid:'2',
                    //expvalue:'2',
                    expvalue:{id:expense.category.id },
                    expteam:{team_id:expense.team.team_id},
                    expdate : new Date(expense.expensedate),
                    
                   //expdate : Moment(expense.expensedate).toDate(),
                    explocation :expense.location
                });
            });
        }        
       
    }
    render() {
        const title =<h3>Addd Expense</h3>;
        const {Categories} =this.state;
        const {Teams} = this.state;

        const {Expsenses,isLoading} = this.state;
       
        let optionList  =
                Categories.map( (category) =>
                    <option value={category.id} key={category.id} label={category.name}>
                                
                    </option>
                )

        let optionListTeam  =
        Teams.map( (team) =>
            <option value={team.team_id} key={team.team_id} label={team.team_name}>
                        
            </option>
        )
        
        return (
            <div>
                <br></br>
                   <div className = "container">
                        <div className = "row">
                            <div className = "card col-md-6 offset-md-3 offset-md-3">
                            {
                                    this.getTitle()
                                }
                                <div className = "card-body">
                                    <form>
                                        <div className = "form-group">
                                            <label> Description: </label>
                                            <input placeholder="Description" name="expdesc" className="form-control" 
                                                value={this.state.expdesc} onChange={this.changeExpDescHandler}/>
                                        </div>

                                        <div className = "form-group">
                                            <label> Category: </label>
                                           
                                            <select   name="catid"  value={this.state.expvalue.id} onChange={this.changeExpCatHandler}> 
                                            <option value={-1}> Seleccione una opcion:</option>   
                                            {optionList}
                                                                                
                                           
                                            </select>

                                        </div>
                                       
                                        <div className = "form-group">
                                            <label> Teams: </label>
                                           
                                            <select   name="teamid"  value={this.state.expteam.team_id} onChange={this.changeExpTeamHandler}> 
                                            <option value={-1}> Seleccione un team:</option>   
                                            {optionListTeam}
                                                                                
                                           
                                            </select>

                                        </div>

                                        <div className = "form-group">
                                             <Label for="city">Date</Label>
                                             <DatePicker  name="expdate"  selected={this.state.expdate}  onChange={this.handleExpDateChange} />
                                        </div>
                                        
                                        <div className = "form-group">
                                            <label> Location: </label>
                                            <input placeholder="Location" name="explocation" className="form-control" 
                                                value={this.state.explocation} onChange={this.changeExpLocationHandler}/>
                                        </div>

                                        <button className="btn btn-success" onClick={this.saveOrUpdateExpense}>Save</button>
                                        <button className="btn btn-danger" onClick={this.cancel.bind(this)} style={{marginLeft: "10px"}}>Cancel</button>
                                    </form>
                                </div>
                            </div>
                        </div>

                   </div>
            </div>
        )
    }
}

export default CreateExpenseComponent