import React, { Component } from 'react'
import TeamService from '../../services/TeamService';

class CreateTeamComponent extends Component {
    constructor(props) {
        super(props)

        this.state = {
            // step 2
            team_id: this.props.match.params.team_id,
            team_name: ''
            
        }
        this.changeTeamNameHandler = this.changeTeamNameHandler.bind(this);
        
        this.saveOrUpdateTeam = this.saveOrUpdateTeam.bind(this);
    }

    // step 3
    componentDidMount(){

        // step 4
        if(this.state.id === '_add'){
            return
        }else{
            TeamService.getTeamById(this.state.team_id).then( (res) =>{
                let team = res.data;
                this.setState({team_name: team.team_name
                    
                });
            });
        }        
    }
    saveOrUpdateTeam = (e) => {
        e.preventDefault();
        let team = {team_name: this.state.team_name};
        console.log('team => ' + JSON.stringify(team));

        // step 5
        if(this.state.team_id === '_add'){
            TeamService.createTeam(team).then(res =>{
                this.props.history.push('/team');
            });
        }else{
            TeamService.updateTeam(team, this.state.team_id).then( res => {
                this.props.history.push('/team');
            });
        }
    }
    
    changeTeamNameHandler= (event) => {
        this.setState({team_name: event.target.value});
    }


    cancel(){
        this.props.history.push('/team');
    }

    getTitle(){
        if(this.state.team_id === '_add'){
            return <h3 className="text-center">Add Team</h3>
        }else{
            return <h3 className="text-center">Update Team</h3>
        }
    }
    render() {
        return (
            <div>
                <br></br>
                   <div className = "container">
                        <div className = "row">
                            <div className = "card col-md-6 offset-md-3 offset-md-3">
                                {
                                    this.getTitle()
                                }
                                <div className = "card-body">
                                    <form>
                                        <div className = "form-group">
                                            <label> Team Name: </label>
                                            <input placeholder="Team Name" name="team_name" className="form-control" 
                                                value={this.state.team_name} onChange={this.changeTeamNameHandler}/>
                                        </div>
                                        <button className="btn btn-success" onClick={this.saveOrUpdateTeam}>Save</button>
                                        <button className="btn btn-danger" onClick={this.cancel.bind(this)} style={{marginLeft: "10px"}}>Cancel</button>
                                    </form>
                                </div>
                            </div>
                        </div>

                   </div>
            </div>
        )
    }
}

export default CreateTeamComponent