import React, { Component } from 'react'

export default class comp_files extends Component {

    render() {
      return (
        <div>
          <h4>Files dwh</h4>


          <div class="container">
          <div class="card-deck mb-3 text-center">
            <div class="card mb-4 shadow-sm">
              <div class="card-header">
                <h4 class="my-0 font-weight-normal">EXCEL</h4>
              </div>
              <div class="card-body">
                
               
                <button type="button" class="btn btn-lg btn-block btn-outline-primary">UPLOAD</button>
              </div>
            </div>
            <div class="card mb-4 shadow-sm">
              <div class="card-header">
                <h4 class="my-0 font-weight-normal">CSV</h4>
              </div>
              <div class="card-body">
              
                <button type="button" class="btn btn-lg btn-block btn-primary">UPLOAD</button>
              </div>
            </div>
            <div class="card mb-4 shadow-sm">
              <div class="card-header">
                <h4 class="my-0 font-weight-normal">JSON</h4>
              </div>
              <div class="card-body">
               
                <button type="button" class="btn btn-lg btn-block btn-primary">UPLOAD</button>
              </div>
            </div>
          </div>
        </div>
        </div>
      )
    }
  }