import React, { Component } from 'react'
import TeamService from '../../services/TeamService'

class ListTeamComponent extends Component {
    constructor(props) {
        super(props)

        this.state = {
                teams: []
        }
        this.addTeam = this.addTeam.bind(this);
        this.editTeam = this.editTeam.bind(this);
        this.deleteTeam = this.deleteTeam.bind(this);
    }

    deleteTeam(team_id){
        TeamService.deleteTeam(team_id).then( res => {
            this.setState({teams: this.state.teams.filter(team => team.team_id !== team_id)});
        });
    }
    viewTeam(team_id){
        this.props.history.push(`/view-team/${team_id}`);
    }
    editTeam(team_id){
        this.props.history.push(`/add-team/${team_id}`);
    }

    componentDidMount(){
        TeamService.getTeams().then((res) => {
            this.setState({ teams: res.data});
        });
    }

    addTeam(){
        this.props.history.push('/add-team/_add');
    }

    render() {
        return (
            <div>
                 <h2 className="text-center">Teams List</h2>
                 <div className = "row">
                    <button className="btn btn-primary" onClick={this.addTeam}> Add Teams</button>
                 </div>
                 <br></br>
                 <div className = "row">
                        <table className = "table table-striped table-bordered">

                            <thead>
                                <tr>
                                    <th> Team id</th>
                                    <th> Team Name</th>
                                    <th> Actions</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    this.state.teams.map(
                                        team => 
                                        <tr key = {team.team_id}>
                                             <td> { team.team_id} </td>
                                             <td> { team.team_name} </td>   
                                             <td>
                                                 <button onClick={ () => this.editTeam(team.team_id)} className="btn btn-info">Update </button>
                                                 <button style={{marginLeft: "10px"}} onClick={ () => this.deleteTeam(team.team_id)} className="btn btn-danger">Delete </button>
                                                 <button style={{marginLeft: "10px"}} onClick={ () => this.viewTeam(team.team_id)} className="btn btn-info">View </button>
                                             </td>
                                        </tr>
                                    )
                                }
                            </tbody>
                        </table>

                 </div>

            </div>
        )
    }
}

export default ListTeamComponent